.div-container{
  position: fixed;
  z-index: 3;
}
.Modal-box{
  width: 200px;
  background: #2a2e4b;
  border: none;
  border-radius: 9px;
  -webkit-box-shadow: inset 0 -0.5em rgba(0,0,0 , 0.3);
          box-shadow: inset 0 -0.5em rgba(0,0,0 , 0.3);
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  color: aliceblue;
  font-family: Arial;
  font-size: 16px;
  will-change: transform;
  -webkit-animation-name: ModalKeyFrames;
          animation-name: ModalKeyFrames;
  margin: 0 auto;
  position: fixed;
  left: 0;
  right: 0;
  top: 20%;
  padding: .5em;
  display: inline-block;
  will-change: transform;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
}
.Modal-box:active{
  -webkit-transform: scale(.9);
      -ms-transform: scale(.9);
          transform: scale(.9);
}

@-webkit-keyframes ModalKeyFrames {
  from {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
  }
  20% {
    -webkit-transform: translateY(-35px);
            transform: translateY(-35px);
  }
  40% {
    -webkit-transform: translateY(25px);
            transform: translateY(25px);
  }
  60%{
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
  80% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0)
  }
}

@keyframes ModalKeyFrames {
  from {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
  }
  20% {
    -webkit-transform: translateY(-35px);
            transform: translateY(-35px);
  }
  40% {
    -webkit-transform: translateY(25px);
            transform: translateY(25px);
  }
  60%{
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
  80% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0)
  }
}


