.Button{
  text-align: center;
  background-color: lightgray;
  padding: .6em .8em 0.8em 0.8em;
  border: none;
  color: white;
  margin: 5px;
  border-radius: 5px;
  font-size: 15px;
  cursor: pointer;
  -webkit-box-shadow: inset 0 -.2em rgba(0,0,0 , 0.3);
          box-shadow: inset 0 -.2em rgba(0,0,0 , 0.3);
  outline: 0;
  -webkit-transition: .7s;
  -o-transition: .7s;
  transition: .7s;
  will-change: transform;
}
.Button:active{
  -webkit-transform: scale(0.6);
      -ms-transform: scale(0.6);
          transform: scale(0.6);
}
.Button.danger{
  background: red;
}
.Button.primary{
  background: #7dc800;
}


